  position: relative;
  border-radius: 3px;
}

.left {
  float: left;
  padding-left: rem(20);
  margin-top: rem(-50);
}

.right {
  margin-left: rem(140);
  padding-top: rem(40);
  padding-right: rem(20);
  padding-bottom: 1px;
}
