@import 'assets/styles/mixins.scss';

.avatar {
  width: rem(35);
  height: rem(35);
  background-color: $gray-lighten;
  border-color: $gray-darken;
  display: inline-block;
  overflow: hidden;
  border-radius: 50%;

  img {
    width: 100%;
    max-width: 100%;
    height: auto;
  }
}

.avatarImage {
  opacity: 1;
  display: block;
  width: 100%;
  height: 100%;
  max-width: 100%;
  transition: .5s ease;
  backface-visibility: hidden;
  object-fit: cover;
}

.avatarContainer {
  position: relative;
  width: 50%;
  height: rem(35);
  background-color: $gray-lighten;
  border-color: $gray-darken;
  display: inline-block;
  overflow: hidden;
  border-radius: 50%;
}

.avatarOverlay {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  cursor: pointer;
}

.avatarContainer:hover .avatarImage {
  opacity: 0.3;
}

.avatarContainer:hover .avatarOverlay {
  opacity: 1;
}

.avatarUploader {
  transition: .5s ease;
  opacity: 0.3;
  margin: 0;
}

.icon {
  text-align: center;
  margin-bottom: 0;
  i {
    font-size: rem(35);
  }
}

.size20 {
  width: rem(20);
  height: rem(20);
}

.size25 {
  width: rem(25);
  height: rem(25);
}

.size50 {
  width: rem(50);
  height: rem(50);
}

.size70 {
  width: rem(70);
  height: rem(70);
}

.size90 {
  width: rem(90);
  height: rem(90);
}

.size110 {
  width: rem(110);
  height: rem(110);
}

.border {
  border: 4px solid $gray;
}
